:root {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: #000;
}

body {
  padding: 0;
  margin: 0;
}

.play-button {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  background: rgba(0, 0, 0, 0.4);
  cursor: pointer;
  text-align: center;
}
.play-button .pause, .play-button .play {
  font-size: 3rem;
}
.play-button .ended {
  font-size: 1rem;
}
.play-button .ended .fas {
  font-size: 2rem;
}

.video-js {
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  --bg: rgba(0,0,0,0.75);
  --bg-hover: rgba(41, 41, 41, 0.75);
  --bg-selected: rgba(255,255,255, 0.75);
  --progress-bg-color: rgba(124,124,124,.5);
  --progress-color: #fff;
  --control-height: 4em;
  --control-width: 4em;
  --volume-control-width: 6em;
  --transition-in: visibility 0.2s, opacity 0.2s;
  --transition-out: visibility 0.6s, opacity 0.6s;
  /* .vjs-progress-control {
