    justify-content: center;
    height: var(--control-height) !important;
  } */
  /* .vjs-volume-panel .vjs-volume-control.vjs-volume-vertical {
    left: -var(--control-width) !important;
    width: var(--control-width) !important;
    height: 10em !important;
    bottom: 10em !important;
    z-index:10;
    .vjs-volume-bar.vjs-slider-vertical {
      width: 0.3em;
      height: calc(100% - var(--control-width));
      margin: 1.35em auto;
    }
  } */
  /* .vjs-level-select, .vjs-playback-rate {
    .vjs-menu li {
      text-transform: none !important;
    }
  } */
}
@media screen and (max-width: 640px) {
  .video-js {
    --control-height: 3em;
    --control-width: 3em;
    --volume-control-width: 5em;
  }
  .video-js .vjs-stop-control, .video-js .vjs-picture-in-picture-control {
    display: none;
  }
}
.video-js .vjs-playback-rate {
  display: block !important;
}
.video-js .logo {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 70px;
  opacity: 0;
  z-index: 2;
}
.video-js .logo img {
  width: 100%;
  height: auto;
}
.video-js .logo:hover {
  opacity: 1 !important;
}
.video-js .vjs-control-bar, .video-js .logo {
  transition: var(--transition-in);
}
.video-js.vjs-user-inactive .vjs-control-bar, .video-js.vjs-user-inactive .logo {
  transition: var(--transition-out);
}
.video-js.vjs-has-started:hover .logo, .video-js.vjs-has-started.vjs-paused .logo, .video-js.vjs-has-started.vjs-user-active .logo {
  opacity: 0.6;
}
.video-js .vjs-big-play-button {
  display: none !important;
}
.video-js video {
  transition: all 0.2s;
}
.video-js .vjs-control-bar, .video-js .vjs-menu-content {
  background: var(--bg) !important;
}
.video-js .vjs-volume-bar {
  background: var(--progress-bg-color) !important;
}
.video-js .js-focus-visible .vjs-menu li.vjs-menu-item:hover, .video-js .vjs-menu li.vjs-menu-item:focus, .video-js .vjs-menu li.vjs-menu-item:hover {
  background: var(--bg-hover) !important;
}
.video-js .js-focus-visible .vjs-menu li.vjs-selected:hover, .video-js .vjs-menu li.vjs-selected, .video-js .vjs-menu li.vjs-selected:focus, .video-js .vjs-menu li.vjs-selected:hover {
  background: var(--bg-selected) !important;
}
.video-js .vjs-play-progress.vjs-slider-bar .vjs-time-tooltip {
  display: none !important;
}
.video-js .vjs-control {
  height: var(--control-height);
  width: var(--control-width);
}
.video-js .vjs-control .far, .video-js .vjs-control .fas {
  font-size: 1.4em;
}
.video-js .vjs-control-bar {
  height: var(--control-height) !important;
}
.video-js .vjs-seek-to-live-control:focus {
  outline: 0 !important;
}
.video-js .vjs-button > .vjs-icon-placeholder:before {
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.video-js .vjs-mouse-display {
  pointer-events: none;
  user-drag: none;
}
.video-js .vjs-progress-holder {
  transition: none !important;
  background: var(--progress-bg-color);
}
.video-js .vjs-progress-holder .vjs-load-progress {
  background: transparent;
}
.video-js .vjs-progress-holder .vjs-load-progress > div {
  background: rgba(255, 255, 255, 0.25);
}
.video-js .vjs-progress-holder .vjs-play-progress {
  background: var(--progress-color);
}
.video-js .vjs-progress-holder .vjs-play-progress:before {
  display: none;
  content: "";
}
.video-js .vjs-progress-control {
  position: absolute;
  left: 0;
  right: 0;
  width: auto;
  font-size: 0.5em;
  height: 1em;
  top: -1em;
  transition: all 0.2s;
  background: black;
}
.video-js .vjs-progress-control .vjs-progress-holder {
  margin: 0;
}
.video-js:hover .vjs-progress-control {
  font-size: 1.5em;
}
.video-js .vjs-stop-control {
  cursor: pointer;
}
.video-js .vjs-progress-holder {
  height: 100%;
}
.video-js .vjs-progress-holder .vjs-play-progress,
.video-js .vjs-progress-holder .vjs-load-progress {
  position: absolute;
  display: block;
  height: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
}
.video-js .vjs-progress-holder .vjs-play-progress {
  background: #66a8cc url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAAP0lEQVQIHWWMAQoAIAgDR/QJ/Ub//04+w7ZICBwcOg5FZi5iBB82AGzixEglJrd4TVK5XUJpskSTEvpdFzX9AB2pGziSQcvAAAAAAElFTkSuQmCC) -50% 0 repeat;
}
.video-js .vjs-progress-holder .vjs-load-progress {
  background: rgba(255, 255, 255, 0.4);
}
.video-js .vjs-seek-handle {
  width: 1.5em;
  height: 100%;
}
.video-js .vjs-custom-control-spacer {
  display: block !important;
  flex: 1;
}
.video-js .vjs-time-tooltip {
  font-size: 10px !important;
  padding: 0.5em;
  top: -2em;
  right: auto !important;
  width: auto;
  white-space: nowrap;
}
.video-js .vjs-playback-rate {
  /* .vjs-menu {
    width: 6em !important;
    left: 50%;
    transform: translateX(-50%);
  } */
}
.video-js .vjs-playback-rate .vjs-playback-rate-value {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1em !important;
}
.video-js .vjs-quality-selector .vjs-menu-title {
  display: none;
}
.video-js .vjs-volume-panel {
  height: var(--control-height) !important;
  transition: all 0.2s !important;
}
.video-js .vjs-volume-panel.vjs-hover, .video-js .vjs-volume-panel.vjs-slider-active {
  width: calc(var(--control-width) + var(--volume-control-width)) !important;
}
.video-js .vjs-volume-panel.vjs-hover .vjs-volume-control, .video-js .vjs-volume-panel.vjs-slider-active .vjs-volume-control {
  width: var(--volume-control-width) !important;
}
.video-js .vjs-volume-panel .vjs-volume-control {
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--control-height) !important;
  transition: all 0.2s !important;
  margin: 0;
  margin-left: calc(-1 * var(--control-width) * 0.2) !important;
}
.video-js .vjs-volume-panel .vjs-volume-control .vjs-volume-bar {
  width: 100%;
}
.video-js .vjs-volume-panel .vjs-volume-control .vjs-volume-level:before {
  content: "" !important;
}
@media screen and (max-width: 640px) {
  .video-js .vjs-volume-panel.vjs-hover {
    width: var(--control-width) !important;
  }
  .video-js .vjs-volume-panel .vjs-volume-control {
    display: none !important;
  }
}
.video-js *:focus {
  text-shadow: none !important;
  box-shadow: none !important;
}
.video-js .vjs-live-display {
  display: flex;
  height: var(--control-height) !important;
  align-items: center;
}
.video-js .vjs-text-track-display {
  bottom: var(--control-height) !important;
}
.video-js .vjs-remaining-time {
  align-items: center;
  justify-content: center;
  width: auto !important;
  padding-left: 0.5em;
  padding-right: 0.5em;
  min-width: 4em;
}
.video-js:not(.vjs-live) .vjs-remaining-time {
  display: flex;
}
.video-js .vjs-time-display-toggle {
  cursor: pointer;
}
.video-js .seek-to-live-wrapper {
  line-height: 1 !important;
  display: flex;
  align-items: center;
}
.video-js .seek-to-live-wrapper .vjs-seek-to-live-control {
  background: rgba(255, 0, 0, 0.3);
  border-radius: 999px;
  padding: 0 1em;
}
.video-js .seek-to-live-wrapper .vjs-seek-to-live-control:not(.vjs-at-live-edge) {
  background: rgba(120, 120, 120, 0.3);
}
.video-js .seek-to-live-wrapper * {
  line-height: 2.4;
  height: auto;
}
.video-js .vjs-seek-to-live-control {
  width: auto !important;
  padding: 0 0.5em;
}
.video-js .vjs-seek-to-live-control:not(.vjs-at-live-edge) {
  cursor: pointer;
}
.video-js .vjs-modal-dialog-content {
  display: flex;
  align-items: center;
  justify-content: center;
}
.video-js .vjs-menu-button-popup .vjs-menu {
  bottom: 0;
  width: auto;
  height: auto;
  margin-bottom: 0;
  left: 50%;
}
.video-js .vjs-menu-button-popup .vjs-menu .vjs-menu-content {
  bottom: var(--control-height);
  width: auto;
  transform: translateX(-50%);
  max-height: 50vh !important;
}
.video-js .vjs-menu-button-popup .vjs-menu .vjs-menu-content .vjs-menu-item {
  padding: 0.5em 2em;
}
.video-js .vjs-loading-spinner {
  pointer-events: none;
}
.video-js .vjs-menu li.vjs-menu-item {
  user-select: none;
}